import { Query } from "react-apollo";
import Error from "./ErrorMessage.js";
import gql from "graphql-tag";

const ALL_USERS_QUERY = gql`
  query {
    users {
      id
      name
      email
      permissions
    }
  }
`;

const Permissions = props => {
  return (
    <Query query={ALL_USERS_QUERY}>
      {({ data, loading, error }) =>
        console.log(data) || (
          <>
            <Error error={error} />
            <p>Hey!</p>
          </>
        )
      }
    </Query>
  );
};

export default Permissions;
